/* Services.css */
.services {
  padding: 60px 20px;
  background-color: rgba(255, 255, 255, 0.9); /* Slight transparency for a modern look */
  text-align: center;
  color: #1C1C1E; /* Dark gray for text */
}

.services-container {
  display: flex;
  gap: 30px;
  justify-content: center;
  flex-wrap: wrap;
}

.service-card {
  background-color: rgba(255, 255, 255, 0.7); /* Subtle transparency for card background */
  padding: 0;
  border-radius: 12px; /* Slightly more rounded corners */
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1); /* Softer, more pronounced shadow */
  width: 220px;
  height: 320px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.service-card:hover {
  transform: scale(1.05); /* Slight zoom on hover */
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15); /* Stronger shadow on hover */
}

.service-image {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 12px 12px 0 0; /* Rounded top corners */
}

.service-card h3 {
  margin: 20px 15px 10px;
  color: #1C1C1E; /* Dark gray for headings */
  font-size: 1.4rem;
}

.service-card p {
  margin: 0 15px 20px;
  color: #3A3A3C; /* Medium gray for text */
  font-size: 1rem;
  line-height: 1.6;
}

/* Responsive styling for phones */
@media (max-width: 768px) {
  .services-container {
    flex-wrap: nowrap;
    overflow-x: auto;
    padding-bottom: 20px; /* Add padding to prevent cards from cutting off */
  }

  .service-card {
    flex: 0 0 auto;
    width: 180px; /* Adjust width for smaller screens */
    height: 280px; /* Adjust height for smaller screens */
  }
}
